@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
}
@media (min-width: 1200px) {
  html {
    background-color: #f2f2f2;
  }
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.advantage {
  text-transform: uppercase;
  text-align: center;
  background-color: #ffffff;
  max-width: 1200px;
  align-content: center;
  margin: 0 auto;
}
.advantage__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
}
@media (min-width: 768px) {
  .advantage__title {
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
    color: #000000;
    font-size: 21px;
    line-height: 26px;
    max-width: 160px;
    text-align: center;
    align-self: center;
  }
}
.advantage__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .advantage__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    position: relative;
  }
}
.advantage__item--city {
  position: relative;
  width: 100%;
}
.advantage__item--city .advantage__description {
  margin: 0;
}
.advantage__item--city::before {
  content: "";
  width: 100%;
  min-height: 37px;
  display: block;
  position: absolute;
  top: -22px;
  left: 0;
  background: url("../img/white-angle-3-mobile.png") no-repeat center center;
  z-index: 2;
}
@media (min-width: 768px) {
  .advantage__item--city {
    grid-column-start: 1;
    grid-column-end: -1;
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
  .advantage__item--city::before {
    display: none;
  }
  .advantage__item--city .advantage__number {
    margin-top: -9px;
  }
}
.advantage__item--city .advantage__text {
  background-color: #81b3d2;
  color: #ffffff;
  padding: 50px 12px 40px 12px;
  margin: 0;
  min-height: 205px;
}
@media (min-width: 768px) {
  .advantage__item--city .advantage__text {
    min-height: 256px;
    padding: 40px 30px 45px 30px;
  }
}
@media (min-width: 1200px) {
  .advantage__item--city .advantage__text {
    padding: 47px 60px 57px 60px;
  }
}
.advantage__title--white {
  color: #ffffff;
  margin: 0;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .advantage__title--white {
    margin: 0 auto 13px;
  }
}
.advantage__number--white {
  display: inline-block;
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .advantage__number--white {
    margin-bottom: 22px;
  }
}
.advantage__image-wrapper {
  display: block;
  width: 100%;
  min-height: 260px;
  position: relative;
}
@media (min-width: 768px) {
  .advantage__image-wrapper {
    min-height: 256px;
  }
}
.advantage__image {
  object-fit: cover;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
}
.advantage__item--bridge {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
}
.advantage__item--bridge .advantage__description {
  margin: 0;
}
@media (min-width: 768px) {
  .advantage__item--bridge {
    grid-column-start: 1;
    grid-column-end: -1;
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
  .advantage__item--bridge .advantage__number {
    margin-top: -9px;
  }
}
.advantage__image-wrapper--bridge {
  order: 2;
}
@media (min-width: 768px) {
  .advantage__image-wrapper--bridge {
    order: 1;
  }
}
.advantage__text--bridge {
  color: #ffffff;
  background-color: #81b3d2;
  padding: 39px 20px 35px 20px;
  min-height: 191px;
  order: -1;
  margin: 0;
}
@media (min-width: 768px) {
  .advantage__text--bridge {
    padding: 43px 36px 41px 36px;
    min-height: 256px;
    order: 2;
    grid-column-start: 2;
    grid-column-end: 3;
  }
}
@media (min-width: 1200px) {
  .advantage__text--bridge {
    padding: 52px 80px 53px 80px;
  }
}
.advantage__pics-list {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .advantage__pics-list {
    display: grid;
    row-gap: 15px;
    padding-top: 25px;
    max-width: 450px;
    align-self: center;
    margin: 0 auto;
    margin-bottom: 37px;
  }
}
@media (min-width: 768px) {
  .advantage__pics-list {
    grid-column-start: 1;
    grid-column-end: -1;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
