    padding: 158px 41px 58px 41px;
  }
  .advantage__pics-item .advantage__title {
    text-align: center;
    margin: 0;
    margin-left: auto;
    margin-right: auto;
  }
  .advantage__pics-item .advantage__description {
    margin-top: 25px;
  }
}
@media (min-width: 1200px) {
  .advantage__pics-item {
    padding: 158px 58px 79px 58px;
  }
}
.advantage__pics-item--housing {
  background-image: url("../img/icon-house.svg");
  background-repeat: no-repeat;
  background-position: 21px 1px;
  background-size: 39px 37px;
}
@media (min-width: 768px) {
  .advantage__pics-item--housing {
    background-position: 50% 60px;
    background-size: 75px 72px;
  }
}
.advantage__pics-item--souvenirs {
  flex-shrink: 1;
  flex-grow: 0;
  flex-basis: 84px;
  background-image: url("../img/icon-gift.svg");
  background-repeat: no-repeat;
  background-position: 25px 0;
  background-size: 32px 38px;
}
@media (min-width: 768px) {
  .advantage__pics-item--souvenirs {
    order: 3;
    background-position: 50% 57px;
    background-size: 64px 75px;
    padding-left: 32px;
    padding-right: 32px;
  }
}
.advantage__pics-item--food {
  order: 3;
  flex-basis: 105px;
  flex-shrink: 1;
  flex-grow: 0;
  background-image: url("../img/icon-burger.svg");
  background-repeat: no-repeat;
  background-position: 18px 7px;
  background-size: 40px 35px;
}
@media (min-width: 768px) {
  .advantage__pics-item--food {
    order: 2;
    background-position: 50% 63px;
    background-size: 76px 68px;
  }
}
.advantage__item--grey {
  background-color: #eeeeee;
  padding: 25px 35px 10px 35px;
}
.advantage__item--grey::after {
  content: "";
  height: 1px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 20px;
  right: 20px;
  background-color: #d6d6d6;
}
.advantage__item--grey:last-child::after {
  display: none;
}
@media (max-width: 767px) {
  .advantage__item--grey {
    width: 100%;
    min-height: 167px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .advantage__item--grey .advantage__title {
    margin: 0;
  }
  .advantage__item--grey .advantage__number {
    margin-top: 6px;
  }
}
@media (min-width: 768px) {
  .advantage__item--grey {
    min-height: 256px;
    padding: 21px 33px 27px 33px;
    align-self: stretch;
  }
  .advantage__item--grey::after {
    display: none;
  }
}
@media (min-width: 1200px) {
  .advantage__item--grey {
    padding: 21px 50px 27px 50px;
  }
}
@media (min-width: 768px) {
  .advantage__item--grey .advantage__title {
    max-width: 150px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 13px;
    align-self: center;
    text-align: center;
  }
  .advantage__item--grey .advantage__description {
    margin-top: 23px;
  }
}
@media (max-width: 767px) {
  .no-mobile {
