.map::before {
  content: "";
  width: 27px;
  height: 27px;
  display: block;
  position: absolute;
  background-color: #81b3d2;
  border: 5px solid #ffffff;
  border-radius: 50%;
  left: 45%;
  top: 46%;
  z-index: 1;
}
@media (min-width: 768px) {
  .map::before {
    top: 47%;
    left: 48%;
  }
}
.map iframe {
  position: relative;
  z-index: 2;
  border: none;
}
/* page-body */
.page-body {
  position: relative;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  font-weight: normal;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-body {
    min-width: 768px;
  }
}
@media (min-width: 1200px) {
  .page-body {
    width: 1200px;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}
.page-footer {
  width: 100%;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1200px;
